.page {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  min-height:  100vh;
  position: fixed;
  left: 0;
  right: 0;
  background-image: linear-gradient( 110.7deg,  rgba(9,154,151,1) 6.3%, rgba(21,205,168,1) 90.6% );
}
@media screen and (max-width: 500px) {
  .page{
    width: 100vw;
  }
}


.form__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 200px;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 100px;
  border-bottom-left-radius: 100px;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  max-width: -webkit-fit-content;
  max-width: -moz-fit-content;
  max-width: fit-content;
  margin: auto;
  height: 750px;
  padding: 70px 20px;
  box-sizing: border-box;
  background: rgb(22,22,22,.4);
  background: linear-gradient(30deg, rgba(22,22,22,.9) 0%, rgb(51, 50, 50,.9) 59%, rgb(17, 17, 17, .9) 100%);
  position: relative;
  box-shadow: 5px 15px 50px -4px rgb(43, 43, 43, .2);
  overflow: hidden;
  transition: all 0.3s 0s linear;
}
.form__title{
margin: 10px auto 0px;
padding-bottom: 15px;
text-shadow: 1px 1px 20px rgb(76, 76, 76,.4);
transition: all 0.3s 0s linear;

}
.form__subtitle{
  margin: auto;
  color: rgb(226, 226, 226);
  transition: all 0.3s 0s linear;

}
.fieldset {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: none;
  width: 400px;

}

.form__heading {
  font-weight: 400;
  font-size: 31px;
  text-align: center;
  color: rgb(219, 219, 219);
  transition: all 0.3s 0s linear;

}
.form__heading_underline{
  max-width: 80%;
  height: 8px;
  border-radius: 10px;
  background: rgb(79,144,132);
  background-image: radial-gradient( circle 590px at 8.2% 13.8%,  rgb(231, 231, 231) 0%, rgb(190, 190, 190) 90% );  border: none;
  margin: 0px auto 40px;
  box-shadow: 1px 1px 12px -1px rgb(62, 62, 62, .4);
}

.form-field {
  position: relative;
  box-sizing: border-box;
  background-image: linear-gradient( 110deg,  rgba(12,173,157, 100) 16.3%, rgba(16,187,162, 0.896) 90.6% );
  margin-top: 30px;
  margin-bottom: 8px;
  max-width: 100%;
  font-size: 18px;
  padding: 15px 0px 15px 20px;
  border-top-left-radius: 5px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 20px;
  font-weight: 200;
  color: rgb(251, 251, 251);
  border: none;
  transition: all 0.3s 0s linear;

}
.form-field:hover{
  transition: all 0.3s 0s linear;
background-blend-mode:soft-light;
background-color: rgb(174, 202, 35);
color: black;
z-index: 2;
}

.form-field::-webkit-input-placeholder {
  color: rgb(242, 242, 242);
  font-size: 20px;
}

.form-field::placeholder {
  color: rgb(242, 242, 242);
  font-size: 20px;
}
.form-field:hover::-webkit-input-placeholder{
  color: black;
}
.form-field:hover::placeholder{
  color: black;
}

.name__content {
  display: flex;
  flex-direction: row;
}

.select {
  text-align: center;
  width: 100%;
}
.select__default {
  text-align: left;
  margin-left: 20px;
}
.submit__button {
  background-color: rgb(238, 238, 238);
  position: absolute;
  bottom: -1px;
  margin: 0px;
  border-radius: 10px;
  width: 104%;
  height: 85px;
  
  opacity: 0.75;
  box-shadow: inset  0px 6px 12px -1px rgb(32, 32, 32, .3);
  border: none;
  font-weight: 200;
  font-size: 24px;
  text-align: center;
  transition: all 0.5s 0s linear;
}
.submit__button_valid{
  opacity: 1;
  color: black;

}
.submit__button_background{
  position: absolute;
  right: 100%;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 85px;
  z-index: 1;
  transition: all 0.5s 0s ease-out;
}

.submit__button:hover  .submit__button_background {
  opacity: 1;
 right: 0;
 background: rgb(98,226,42);
 background: linear-gradient(30deg, rgba(98,226,42,1) 0%, rgba(157,219,75,1) 100%);
 transition: all .7s 0s ease-out;
}
.submit__button:hover .submit__button_icon{
  opacity: 1;
  right: 60px;
  transition: all 0.6s 0.5s ease-out;
  z-index: 2;
}
 .submit__button_text{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 30px;
  z-index: 2;
}
.submit__button_icon{
  background-image: url(/fetch-frontend-form/static/media/double__arrow.d695df67.svg);
  width: 50px;
  height: 50px;
  background-size: contain;
  position: absolute;
  right: 90px;
  bottom: 17px;
  opacity: 0;
}
.submit__invalid{
  color: rgb(162, 162, 162);

}
.submit__button:hover .submit__button_text{
 transition: all 0.4s 0s ease-out;
}
.submit__button_background:active{
  background: unset;
  background-color: green;
}


.form-field ~ .form__error{
  position: relative;
}
.form__error {
  color: rgb(32, 205, 159);
  padding-left: 20px;
  position: absolute;
}
@media screen and (min-width: 1800px) {
  .form-field {
    font-size: 26px;
  }
}
@media screen and (max-width: 580px) {
  .form{
    margin-bottom: 150px;
  }
}

@media screen and (max-width: 500px) {
  .form{
    width: 100vw;
    height: 680px;
    border-radius: unset;
    margin-bottom: 100px;
  }
  .form__title{
    font-size: 28px;
    font-weight: 500;
  }
  .form__subtitle{
    font-weight: 200;
    font-size: 22px;
  }

  .form-field{
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    width: 90vw;
    font-size: 14px;
  }
  .form-field::-webkit-input-placeholder{
    font-size: 14px;
  }
  .form-field::placeholder{
    font-size: 14px;
  }
  .form__error{
    font-size: 12px;
    width: 90vw;
    margin: auto;
  }
  .form__heading_underline{
    height: 5px;
  }
}
.footer{
    color: rgb(243, 243, 243);
    width: 100%;
    height: 200px;
 
    position: absolute;
    bottom: 0;
}
.footer__content{
    max-width: 1600px;
    margin: auto;
    display: flex;
}
.credits{
  display: flex;
  position: absolute;
  right: 50px;
  bottom: 50px;
}
.credit__item{
    text-align: center;
    margin-left: 50px;
    margin-right: 50px;
    padding-top: 80px;
    display: flex;
    flex-direction: column;
}
.credit__link{
    color: rgb(154, 253, 212);
    text-decoration: none;
    font-weight: 500;
}
.credit__text{
    font-weight: 200;
    margin-top: 5px;
    font-size: 14px;
}
.copyright{
    position: absolute;
    left: 50px;
    bottom: 50px;
    font-weight: 700;
    color: rgb(154, 253, 212);
    
}
@media screen and (max-width: 1400px) {
    .credits{
        width: 70%;
    }
    .credit__item{
        margin-left: auto;
        margin-right: auto;
        max-width: 200px;
    }
    .copyright{
        bottom: 60px;
    }
}

@media screen and (max-width: 1080px) {
    .credits{
        width: 60%;
    }
    .credit__text{
        font-size: 12px;
        max-width: 150px;
    }
    .credit__link{
        margin: 0px;
        font-size: 14px;
    }
        
}
@media screen and (max-width: 850px) {
    .credits{
        margin: 0 auto;
        left: 0;
        right: 0;
     bottom: 100px;
     width: 90%;
    }
    .copyright{
        left: 0;
        right: 0;
        bottom: 30px;
        text-align: center;
    }
    .credit__item{
        padding-top: 10px;
        width: 150px;
    }
}

@media screen and (max-width: 580px) {
   .footer{
    height: 380px;
   }
    .footer__content{
        flex-direction: column;
    }
    .credits{
        flex-direction: column;
    }
    .credit__item{
        max-width: unset;
        width: unset;
    }
    .credit__link{
        font-size: 16px;
    }
    .credit__text{
        font-size: 14px;
        width: unset;
        max-width: unset;
    }
    

}
.modal__background {
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgb(15, 15, 15, 0.6);
}
.modal__container {
  position: relative;
  border-radius: 10px;
  margin: auto;
  visibility: visible;
  border-top-right-radius: 110px;
  border-bottom-left-radius: 110px;
  border-top-left-radius: 16px;
  border-bottom-right-radius: 16px;
  width: 600px;
  height: 400px;
  z-index: 2;
  background-color: rgb(91, 187, 200);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: 1px 15px 150px 2px rgba(46, 45, 45, 0.6);
}
.modal__hidden {
  display: none;
}
.modal__text-container{
position: absolute; 
left: 60px;
top: 35%;
}


.modal__close-button {
  background-image: url(/fetch-frontend-form/static/media/blackMobileX.ec62b2d2.svg);
  width: 50px;
  height: 50px;
  background-position: center;
  background-size: cover;
  background-color: transparent;
  position: absolute;
  border: none;
  top: -20px;
  right: -20px;
}

.modal__icon{
  opacity: .2;
  background-repeat: no-repeat;
  z-index: 2;
}
.modal__message {
  color: rgb(199, 199, 199);
  font-size: 16px;
  text-align: left;
  font-weight: 500;
  margin-bottom: 16px;
}
.modal__sub-message {
  color: white;
  margin-top: 0px;
  font-size: 22px;
  font-weight: 700;
  text-align: left;
}
.fail-icon{
  background-image: url(/fetch-frontend-form/static/media/oops__icon.8932b615.svg);
  background-position: top;
  object-position: 20% 70%;
  position: absolute;
  bottom: 0px;
  right: -0px;
  background-size: cover;
  width: 450px;
  height: 350px;
}
.modal__success-message{
  font-size: 28px;
  font-weight: 700;
  color: white;
  position: relative;
  right: 20px;
  bottom: 100px;
}
.success-icon{
  background-image: url(/fetch-frontend-form/static/media/success__icon.250a500a.svg);
  position: absolute;
  bottom: 10px;
  right: -80px;
  width: 420px;
  height: 240px;



}
body {
  margin: 0;
  padding: 0;
}
* {
  font-family: "Inter", sans-serif;
}

