
.form__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-bottom: 200px;
}
.form {
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top-right-radius: 100px;
  border-bottom-left-radius: 100px;
  border-top-left-radius: 20px;
  border-bottom-right-radius: 20px;
  max-width: fit-content;
  margin: auto;
  height: 750px;
  padding: 70px 20px;
  box-sizing: border-box;
  background: rgb(22,22,22,.4);
  background: linear-gradient(30deg, rgba(22,22,22,.9) 0%, rgb(51, 50, 50,.9) 59%, rgb(17, 17, 17, .9) 100%);
  position: relative;
  box-shadow: 5px 15px 50px -4px rgb(43, 43, 43, .2);
  overflow: hidden;
  transition: all 0.3s 0s linear;
}
.form__title{
margin: 10px auto 0px;
padding-bottom: 15px;
text-shadow: 1px 1px 20px rgb(76, 76, 76,.4);
transition: all 0.3s 0s linear;

}
.form__subtitle{
  margin: auto;
  color: rgb(226, 226, 226);
  transition: all 0.3s 0s linear;

}
.fieldset {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  border: none;
  width: 400px;

}

.form__heading {
  font-weight: 400;
  font-size: 31px;
  text-align: center;
  color: rgb(219, 219, 219);
  transition: all 0.3s 0s linear;

}
.form__heading_underline{
  max-width: 80%;
  height: 8px;
  border-radius: 10px;
  background: rgb(79,144,132);
  background-image: radial-gradient( circle 590px at 8.2% 13.8%,  rgb(231, 231, 231) 0%, rgb(190, 190, 190) 90% );  border: none;
  margin: 0px auto 40px;
  box-shadow: 1px 1px 12px -1px rgb(62, 62, 62, .4);
}

.form-field {
  position: relative;
  box-sizing: border-box;
  background-image: linear-gradient( 110deg,  rgba(12,173,157, 100) 16.3%, rgba(16,187,162, 0.896) 90.6% );
  margin-top: 30px;
  margin-bottom: 8px;
  max-width: 100%;
  font-size: 18px;
  padding: 15px 0px 15px 20px;
  border-top-left-radius: 5px;
  border-top-right-radius: 20px;
  border-bottom-right-radius: 5px;
  border-bottom-left-radius: 20px;
  font-weight: 200;
  color: rgb(251, 251, 251);
  border: none;
  transition: all 0.3s 0s linear;

}
.form-field:hover{
  transition: all 0.3s 0s linear;
background-blend-mode:soft-light;
background-color: rgb(174, 202, 35);
color: black;
z-index: 2;
}

.form-field::placeholder {
  color: rgb(242, 242, 242);
  font-size: 20px;
}
.form-field:hover::placeholder{
  color: black;
}

.name__content {
  display: flex;
  flex-direction: row;
}

.select {
  text-align: center;
  width: 100%;
}
.select__default {
  text-align: left;
  margin-left: 20px;
}
.submit__button {
  background-color: rgb(238, 238, 238);
  position: absolute;
  bottom: -1px;
  margin: 0px;
  border-radius: 10px;
  width: 104%;
  height: 85px;
  
  opacity: 0.75;
  box-shadow: inset  0px 6px 12px -1px rgb(32, 32, 32, .3);
  border: none;
  font-weight: 200;
  font-size: 24px;
  text-align: center;
  transition: all 0.5s 0s linear;
}
.submit__button_valid{
  opacity: 1;
  color: black;

}
.submit__button_background{
  position: absolute;
  right: 100%;
  top: 0;
  bottom: 0;
  width: 100%;
  height: 85px;
  z-index: 1;
  transition: all 0.5s 0s ease-out;
}

.submit__button:hover  .submit__button_background {
  opacity: 1;
 right: 0;
 background: rgb(98,226,42);
 background: linear-gradient(30deg, rgba(98,226,42,1) 0%, rgba(157,219,75,1) 100%);
 transition: all .7s 0s ease-out;
}
.submit__button:hover .submit__button_icon{
  opacity: 1;
  right: 60px;
  transition: all 0.6s 0.5s ease-out;
  z-index: 2;
}
 .submit__button_text{
  position: absolute;
  left: 0;
  right: 0;
  bottom: 30px;
  z-index: 2;
}
.submit__button_icon{
  background-image: url(../../vendor/double__arrow.svg);
  width: 50px;
  height: 50px;
  background-size: contain;
  position: absolute;
  right: 90px;
  bottom: 17px;
  opacity: 0;
}
.submit__invalid{
  color: rgb(162, 162, 162);

}
.submit__button:hover .submit__button_text{
 transition: all 0.4s 0s ease-out;
}
.submit__button_background:active{
  background: unset;
  background-color: green;
}


.form-field ~ .form__error{
  position: relative;
}
.form__error {
  color: rgb(32, 205, 159);
  padding-left: 20px;
  position: absolute;
}
@media screen and (min-width: 1800px) {
  .form-field {
    font-size: 26px;
  }
}
@media screen and (max-width: 580px) {
  .form{
    margin-bottom: 150px;
  }
}

@media screen and (max-width: 500px) {
  .form{
    width: 100vw;
    height: 680px;
    border-radius: unset;
    margin-bottom: 100px;
  }
  .form__title{
    font-size: 28px;
    font-weight: 500;
  }
  .form__subtitle{
    font-weight: 200;
    font-size: 22px;
  }

  .form-field{
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
    width: 90vw;
    font-size: 14px;
  }
  .form-field::placeholder{
    font-size: 14px;
  }
  .form__error{
    font-size: 12px;
    width: 90vw;
    margin: auto;
  }
  .form__heading_underline{
    height: 5px;
  }
}